@import "main";
// End Of Import

// Preloader
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $color-prim;
  @include Flex;
  z-index: 9999;

  .spinner {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 4px solid $color-sec;
    border-left: 4px solid #fec918;
    animation: spin 1s linear infinite;
  }
}

// Mob Nav
.menuTriger {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba($color-prim, 0.8);
  position: fixed;
  top: 25px;
  right: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 99;
  display: none;

  html[dir="ltr"] & {
    left: 30px;
    right: unset;
  }

  @include Sm {
    display: flex;
  }

  i {
    color: #ffffff;
    font-size: 20px;
  }
}

.navover {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba($color-sec, 0.95);
  z-index: 9;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;

  &.open {
    opacity: 1;
    visibility: visible;
  }
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: $color-prim;
  z-index: 99;
  overflow-y: auto;
  transition: 0.5s;
  @include Flex;
  flex-direction: column;
  transform-origin: right;
  transform: scaleX(0);

  html[dir="ltr"] & {
    transform-origin: left;
  }

  &.open {
    // transform-origin: left;
    transform: scaleX(1);

    ul {
      opacity: 1;

      li {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  &.origin {
    transform-origin: left;

    html[dir="ltr"] & {
      transform-origin: right;
    }
  }

  .options {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 50px;

    .lang a {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      color: #fff;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background-color: $color-sec;
    }

    .close1 {
      display: flex;
      font-size: 18px;
      color: $color-sec;
      cursor: pointer;
    }
  }

  & > ul {
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0.5s;

    & > li {
      position: relative;
      padding: 10px 0;
      // margin-bottom: 20px;
      transition: 0.5s;
      transform: translateX(10px);
      opacity: 0;

      &:nth-child(1) {
        transition-delay: 0.3s;
      }
      &:nth-child(2) {
        transition-delay: 0.5s;
      }
      &:nth-child(3) {
        transition-delay: 0.7s;
      }
      &:nth-child(4) {
        transition-delay: 0.9s;
      }
      &:nth-child(5) {
        transition-delay: 1.1s;
      }
      &:nth-child(6) {
        transition-delay: 1.3s;
      }

      a {
        width: 100%;
        display: block;
        font-size: 20px;
        color: $color-white;
        transition: 0.5s;
      }

      i {
        position: absolute;
        top: 13px;
        left: 13px;
        transform: rotate(90deg);
        font-size: 9px;
        color: currentColor;
        transition: 0.5s;

        html[dir="ltr"] & {
          left: unset;
          right: 13px;
        }

        &.open {
          transform: rotate(0deg);
        }
      }

      .drop {
        width: 100%;
        padding: 5px 15px;
        display: none;
        overflow: visible;
        height: auto;

        li {
          padding: 4px 0;

          a {
            font-size: 15px;
            color: #898989;
            transition: 0.5s;

            &:hover {
              color: #595959;
            }
          }
        }
      }
    }
  }
}

// Start Up

.up {
  background-color: $color-prim;

  @include Sm {
    display: none;
  }

  .details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
  }

  .right {
    span {
      color: $color-white;
      font-size: 13px;

      &:not(:last-child) {
        margin-left: 30px;

        html[dir="ltr"] & {
          margin-left: 0px;
          margin-right: 30px;
        }
      }

      img {
        margin-left: 10px;

        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 10px;
        }
      }
    }
  }

  .lang {
    @include Flex;
    direction: ltr;

    html[dir="ltr"] & {
      direction: rtl;
    }

    a {
      font-size: 12px;
      color: $color-white;
      transition: 0.5s;

      &:hover {
        color: rgba($color-white, 0.7);
      }
    }

    .social {
      margin-left: 20px;
      padding-left: 20px;
      border-left: 1px solid #4d9cd5;
      direction: rtl;

      html[dir="ltr"] & {
        margin-left: 0px;
        padding-left: 0px;
        margin-right: 20px;
        padding-right: 20px;
        border-right: 1px solid #4d9cd5;
        border-left: 0;
        direction: ltr;
      }

      a {
        color: #86c5f3;

        &:not(:last-child) {
          margin-left: 17px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 17px;
          }
        }

        &:hover {
          color: #fff;
        }
      }
    }
  }
}

// End Of Up

// Navbar

.navbar {
  padding: 15px 0;
  margin-bottom: 0;

  .content {
    @include Flex;
    justify-content: space-between;

    @include Sm{
      flex-direction: column;
    }
  }

  .links {
    @include Md {
      margin: auto;
    }

    @include Lg {
      margin-right: auto;
      margin-left: 52px;

      html[dir="ltr"] & {
        margin-right: 52px;
        margin-left: auto;
      }
    }

    @include Sm{
      display: none;
    }

    ul {
      @include Flex;

      li {
        &:not(:last-child) {
          margin-left: 22px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 22px;
          }
        }

        a {
          font-size: 16px;
          color: #3d3d3d;
          transition: 0.5s;

          &:hover {
            color: $color-prim;
          }
        }
      }
    }
  }

  .options {
    @include Flex;

    @include Sm{
      margin-top: 20px;
    }

    & > div {
      position: relative;
      cursor: pointer;

      &:not(:last-child) {
        margin-left: 25px;

        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 25px;
        }

        @include Md {
          margin-left: 15px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 15px;
          }
        }

        @include Lg {
          margin-left: 25px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 25px;
          }
        }
      }

      i {
        color: #3c3c3c;
        font-size: 20px;
        transition: .5s;

        &:hover{
          color: $color-prim;
        }
      }

      &.user {
        &:hover {
          .drop {
            visibility: visible;
            opacity: 1;
            transform: translateX(-50%) translateY(10px);
          }
        }

        .drop {
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%) translateY(20px);
          width: 135px;
          border-radius: 7px;
          background-color: rgb(255, 255, 255);
          text-align: center;
          box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
          opacity: 0;
          visibility: hidden;
          z-index: 9;
          transition: 0.5s;

          &:after {
            content: "";
            width: 0;
            height: 0;
            display: block;
            border-width: 14px;
            border-color: transparent transparent #ffffff;
            border-style: solid;
            top: -19px;
            position: absolute;
            right: 50%;
            transform: translateX(50%);
          }

          ul {
            padding: 10px 13px;

            li {
              padding: 0 12px;

              a {
                color: $color-sec;
                transition: 0.5s;
                font-size: 14px;
                line-height: 2;

                &:hover {
                  color: $color-prim;
                }
              }

              &:not(:last-child) {
                border-bottom: 1px solid #f2f2f2;
              }

              a {
                transition: 0.5s;
              }
            }
          }
        }
      }

      &.search {
        cursor: auto;

        & > i {
          cursor: pointer;
        }

        .search_in {
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(#3c3c3c, 0.98);
          @include Flex;
          opacity: 0;
          visibility: hidden;
          transition: 0.5s;
          z-index: 999;

          & > i {
            position: absolute;
            top: 15%;
            left: 50%;
            transform: translateX(-50%);
            color: $color-prim;
            cursor: pointer;
          }

          .srch {
            width: 70%;
            position: relative;
            transition: 0.5s;
            transform: scaleX(0);
            top: -17%;

            @include Sm{
              width: 90%;
            }

            input {
              display: block;
              margin: auto;
              width: 100%;
              border: 0;
              background-color: rgba(#fff, 0.8);
              height: 50px;
              border-radius: 500px;
              padding: 0 25px;
              transition: 0.5s;
              // transform: scaleX(0);

              &:focus {
                background-color: rgba(#fff, 8);
                transform: translateY(5px);

                & + button {
                  transform: translateY(calc(50% - 20px));
                }
              }
            }

            button {
              border: 0;
              background-color: transparent;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              left: 20px;
              z-index: 9;
              color: $color-prim;
              transition: 0.5s;

              i {
                color: currentColor;
              }

              html[dir="ltr"] & {
                left: unset;
                right: 20px;
              }
            }
          }

          &.open {
            opacity: 1;
            visibility: visible;

            .srch {
              transform: scaleX(1);
            }
          }
        }
      }
    }
  }
}

// Navbar

.owl-nav {
  position: absolute;
  top: 50%;
  transform: translate(50%, -50%);
  right: 50%;
  width: 100%;
  height: 0;
  display: flex !important;
  justify-content: space-between;
  align-items: center;

  @include Sm {
    display: none !important;
  }

  button {
    span {
      line-height: 1;
    }
  }
}

.owl-dots {
  position: absolute;
  bottom: 40px;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  @include Xs {
    bottom: 15px;
  }

  button {
    &:not(:last-child) {
      margin-left: 5px;
    }

    &.active {
      span {
        background-color: $color-prim;
      }
    }

    span {
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: rgba($color-sec, 0.6);
    }
  }
}

// Main Slider

.main-slider {
  margin-top: 0px;

  .owl-nav {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    padding: 0 60px;
    height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include Sm {
      display: none;
    }

    button {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: rgba(0, 0, 0, 0.5) !important;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.5s;

      &:hover {
        background-color: $color-prim !important;
      }

      i {
        font-size: 10px;
        color: #fff;
      }
    }
  }

  .owl-dots {
    position: absolute;
    bottom: 40px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include mob600 {
      bottom: 10px;
    }

    button {
      &:not(:last-child) {
        margin-left: 5px;
      }

      &.active {
        span {
          background-color: $color-prim;
        }
      }

      span {
        display: block;
        width: 8px;
        height: 8px;
        background-color: #757a7b;
        border-radius: 50%;
      }
    }
  }
}

// Main Slider

// Overview

.overview {
  padding: 75px 0;
  text-align: center;

  @include Sm{
    padding: 45px 0;
  }

  h1 {
    margin: 0 0 46px;
    position: relative;
    font-size: 24px;
    font-family: "frtBlk";

    @include Xs{
      line-height: 1.6;
    }

    &:after {
      content: "";
      position: absolute;
      bottom: -25px;
      left: 50%;
      transform: translateX(-50%);
      width: 85px;
      height: 1px;
      background-color: $color-prim;
    }
  }

  p {
    font-size: 17px;
    color: rgb(61, 61, 61);
    font-family: "frtLight";
    line-height: 2;
    margin-bottom: 35px;
  }

  a {
    width: 123px;
    height: 38px;
    border-radius: 100px;
    background-color: #f6f6f6;
    @include Flex;
    margin: auto;
    color: #c3c2c1;
    font-size: 14px;
    font-family: "frtBold";
    position: relative;
    overflow: hidden;
    transition: 0.5s;

    span {
      position: relative;
      z-index: 2;
    }

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $color-prim;
      transform: scaleX(0);
      transform-origin: right;
      transition: 0.5s;
      z-index: 1;

      html[dir="ltr"] & {
        transform-origin: left;
      }
    }

    &:hover {
      color: #fff;

      &:after {
        transform: scaleX(1);
      }
    }
  }
}

// Overview

// Products

.disc.custom {
  padding: 70px 0;

  @include Sm{
    padding: 40px 0;
  }

  &.recent {
    background-color: #f6f6f6;

    @include Sm{
      padding: 40px 0 55px;
    }
  }

  .disc-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;

    & > p {
      font-size: 20px;
      font-family: "frtBold";
      color: #3c3c3c;
      margin-bottom: 0;

      @include mob375 {
        font-size: 20px;
      }
    }
  }

  .owl-carousel {
    .owl-item {
      display: flex;
      justify-content: center;
    }

    .owl-nav {
      position: absolute;
      top: 33%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 105%;
      height: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 30px;
      margin-top: 0;
      color: #c4c4c4;
      padding: 0;
      right: unset;

      @include Md {
        width: 102%;
      }

      @include Lg {
        width: 105%;
      }

      @include Sm {
        display: none;
      }

      button {
        width: 15px;
        transition: 0.5s;

        i {
          font-size: 12px;
          color: #bebebe;
          transition: 0.5s;
        }

        &:hover {
          i {
            color: $color-prim;
          }
        }
      }
    }

    .owl-dots {
      display: none;

      @include Sm {
        display: flex;
        justify-content: center;
        align-items: center;
        position: unset;
        transform: translateY(35px);
      }
    }

    .owl-item {
      img {
        width: initial;
        margin: auto;
      }
    }
  }
}

.product {
  width: 100%;
  position: relative;
  text-align: center;
  background-color: transparent;
  overflow: hidden;
  transition: 0.5s;

  .top {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: $color-prim;
    @include Flex;
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 9;

    html[dir="ltr"] & {
      right: unset;
      left: 10px;
    }

    span {
      font-size: 13px;
      font-family: "frtBold";
      color: #fff;
      line-height: 1;
    }
  }

  .pic {
    height: 217px;
    background-color: $color-white;
    border: 1px solid #e5e5e5;
    @include Flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    perspective: 3000px;

    img {
      transition: 0.3s cubic-bezier(0.6, -0.28, 0.735, 0.045);
      backface-visibility: hidden;
    }

    .cart {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 43px;
      background-color: rgba($color-prim, 0.8);
      @include Flex;
      color: #fff;
      transform-origin: bottom;
      transform: rotateX(92deg);
      transition: 0.3s cubic-bezier(0.6, -0.28, 0.735, 0.045);
      transform-style: preserve-3d;
      cursor: pointer;

      span {
        font-family: "frtBold";
        font-size: 14px;
        color: currentColor;
      }

      i {
        color: currentColor;
        margin-left: 10px;
      }
    }
  }

  .detail {
    padding: 20px 0 0;
    background-color: transparent;

    .rate {
      margin-top: 15px;
      direction: rtl;

      html[dir="ltr"] &{
        direction: ltr;
      }

      i {
        color: #f5cd56;
        margin-left: 3px;
        font-size: 11px;

        &:last-child {
          color: #c9c9c9;
          margin-left: 0;
        }

        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 3px;

          &:last-child{
            margin-right: 0;
          }
        }
      }
    }
  }

  & > a {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  & > img {
    width: initial;
    height: 167px;
    display: block;
    margin: auto;
  }

  .pro-title {
    font-size: 16px;
    color: #3d3d3d;
    transition: 0.5s;
  }

  &:hover {
    .pro-title {
      color: $color-prim;
    }
  }

  span {
    font-size: 16px;
    font-family: "frtBold";
    display: inline-block;
    direction: ltr;

    &.price {
      color: $color-prim;
      margin-right: 8px;
    }

    &.discount {
      color: #a9a9a9;
      text-decoration: line-through;
      font-family: "frt";
    }
  }

  &:hover {
    .pic {
      img {
        transform: scale(0.97);
      }

      .cart {
        transform: none;
      }
    }
  }
}

// Products

// banner

.banner {
  padding: 70px 0;

  @include Sm{
    padding: 40px 0 10px;
  }

  a {
    display: block;
    overflow: hidden;

    img {
      transition: 0.3s cubic-bezier(0.6, -0.28, 0.735, 0.045);
    }

    &:hover {
      img {
        animation: flash 0.5s;
        transform: scale(1.03);
      }
    }
  }
}

// banner

// footer

footer {
  padding: 70px 0 0;
  padding-bottom: 0;
  background-color: #3c3c3c;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 0;
  margin-top: 30px;

  @include Sm {
    padding: 40px 0 0;
  }

  .foot-links {

    // @include Lg {
    //   text-align: right;
    // }

    @include Sm {
      margin-bottom: 15px;
    }

    .collapse {
      display: block;

      @include Sm {
        display: none;
      }

      &.in {
        display: block;
      }
    }

    & > button {
      font-size: 16px;
      font-family: "frtBold";
      color: #fff;
      margin-bottom: 25px;
      background-color: transparent;
      border: 0;
      padding: 0;
      pointer-events: none;

      &.trans {
        &:after {
          content: "\2212";
        }
      }

      @include Sm {
        width: 100%;
        margin: auto;
        pointer-events: all;
        margin-bottom: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      @include mob480 {
        width: 100%;
      }

      &:after {
        content: "\002B";
        margin-left: 5px;
        font-size: 18px;
        float: left;
        display: none;

        @include Sm {
          display: block;
        }
      }
    }

    ul {
      @include Sm {
        padding-top: 10px;
        padding-right: 10px;
      }

      li {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
    }

    a {
      font-size: 14px;
      font-family: "frt";
      backface-visibility: hidden;
      color: #8c8c8c;
      position: relative;
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(
        to right,
        $color-prim,
        $color-prim 50%,
        #8c8c8c 50%
      );
      background-size: 200% 100%;
      background-position: 100%;
      display: inline-block;

      // html[dir="ltr"] & {
      //     background-size: 200% 100%;
      //     background-position: 0%;
      //     background-image: linear-gradient(to left, $color-prim, $color-prim 50%, #998986 50%);
      // }

      &:hover {
        transition: all 0.5s cubic-bezier(0, 0, 0.23, 1);
        background-position: 0%;

        // html[dir="ltr"] & {
        //     background-position: -100%;
        // }
      }

      &:hover {
        color: $color-prim;
      }
    }
  }

  .newsletter {
    @include Md {
      margin-top: 35px;
      text-align: center;
    }

    @include Lg {
      margin-top: 0;
      text-align: right;

      html[dir="ltr"] & {
        text-align: left;
      }
    }

    @include Sm {
      margin-top: 35px;
      text-align: center;
    }

    & > p {
      font-size: 16px;
      font-family: "frtBold";
      color: #fff;
      margin-bottom: 20px;
    }

    & > span {
      font-size: 14px;
      font-family: "frt";
      color: #fff;
      margin-bottom: 15px;
      display: block;
    }
  }

  .frm {
    position: relative;
    margin-bottom: 17px;
    width: 360px;

    @include Md {
      margin: auto;
      margin-bottom: 17px;
    }

    @include Sm {
      margin: auto;
      margin-bottom: 17px;
    }

    @include mob375 {
      width: 280px;
    }

    input {
      padding: 0 20px;
      height: 40px;
      width: 100%;
      border: 0;
      color: #afafaf;
      font-size: 14px;
      background-color: #fff;
      border-radius: 200px;
      transition: 0.5s;

      &::placeholder {
        color: #afafaf;
      }
    }

    button {
      position: absolute;
      top: 50%;
      left: 6px;
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: $color-prim;
      border: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.5s;

      html[dir="ltr"] & {
        left: unset;
        right: 6px;
      }

      &:hover {
        background-color: rgba($color-prim, 0.8);
      }

      i {
        font-size: 12px;
        color: #fff;
      }
    }
  }
}

// End Of Footer

// End

.end {
  border-top: 1px solid #494949;
  margin-top: 80px;

  @include Sm {
    margin-top: 40px;
  }

  @include Xs {
    margin-top: 10px;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mob600 {
      flex-direction: column-reverse;
      padding: 0 0 10px;
    }

    @include Xs {
      text-align: center;
    }

    p,
    a {
      font-size: 13px;
      color: #8c8c8c;
      margin-bottom: 0;

      @include Xs {
        font-size: 13px;
      }

      @include mob600 {
        margin-top: 10px;
      }
    }

    p {
      @include mob480 {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-bottom: 0;
      }
    }

    a {
      img {
        margin-right: 5px;

        // @include mob600 {
        //   margin-right: 0;
        // }

        html[dir="ltr"] & {
          margin-right: 0;
          margin-left: 5px;
        }
      }

      @include mob480 {
        margin-top: 10px;
      }
    }
  }
}

// End
